















import { Component, Vue } from 'vue-property-decorator';

@Component
export default class AdvertisingHomeBanner extends Vue {
  private bannerList: string[] = [
    'banner0.png',
    // 'banner1.png',
    // 'banner2.png'
  ];
  private getBannerImage(img: string) {
    return `
    background: url(${require('@/resources/images/home/'+img)}) no-repeat;
    background-size: 100% 160px;
    `;
  }
}
